<template>
  <div class="wrapper"
       ref="wrapper">
    <div class="content"
         :style="contentStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from '@better-scroll/core'
import Pullup from '@better-scroll/pull-up'

BScroll.use(Pullup)

export default {
  name: 'Scroll',
  props: {
    probeType: {
      type: Number,
      default: 1
    },
    /**
     * 
     */
    pullup: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    pulldown: {
      type: Boolean,
      default: false
    },

    scrollX: {
      type: Boolean,
      default: false
    },

    scrollY: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      scroll: null,
      // wrapperStyle:{
      //   "white-space":this.scrollX?'nowrap':'none'
      // },
      contentStyle: {
        "display": this.scrollX ? 'inline-block' : 'block',
      }
    }
  },

  components: {},

  computed: {},

  beforeMount () { },

  mounted () {
    // 1. scroll 
    //this.$nextTick(() => { })

    this.scroll = new BScroll(this.$refs.wrapper, {
      click: true,
      probeType: this.probeType,
      pullUpLoad: this.pullup,
      scrollY: this.scrollY,
      scrollX: this.scrollX
    })

    // 2. 
    this.scroll.on('scroll', position => {
      //console.log("scroll event")
      this.$emit('scroll', position)
    })

    // 
    if (this.pullup) {
      this.scroll.on('pullingUp', () => {
        // 
        this.$emit('scrollEnd')
      })
    }
  },

  methods: {
    /**
    scrollTo(x,y,time = 300){
      this.scroll.scrollTo(x,y,time)
    },
    */
    scrollTo () {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    finishPullUp () {
      this.scroll && this.scroll.finishPullUp()
    },
    refresh () {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    },
    scrollToElement () {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },

  watch: {}
}
</script>
<style scoped>
.wrapper {
  overflow: hidden;
  position: relative;
}
.content {
  /* display: inline-block; */
  white-space: nowrap;
}
</style>
